@use "base/mixins" as m;
@use "base/variables" as v;

.anfitrion {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 15rem;
  padding: 5rem;
  color: v.$blanco;
  border-radius: 1rem;
  background-image: linear-gradient(
      to bottom,
      v.$negro 0%,
      v.$negro 45%,
      transparent 50%
    ),
    url(../img/anfitrion.jpg);

  background-size: 100%, 77rem;
  background-position: bottom;
  background-repeat: no-repeat;

  @include m.telefono {
    background-size: 100%, 70rem;
    background-position: center, top 200%;
  }
  @include m.tablet {
    grid-template-rows: unset;
    background-image: linear-gradient(
        to right,
        v.$negro 0%,
        v.$negro 50%,
        transparent 55%
      ),
      url(../img/anfitrion.jpg);
    background-size: 100%, auto;
    background-position: center, 50% 30%;
    padding: 15rem 5rem;
  }

  @include m.desktop {
    background-position: center, right center;
  }

  &__container {
  }
  &__heading {
    max-width: 45%;
    @include m.telefono {
      max-width: 100%;
    }
  }
  &__texto {
    color: v.$blanco;

    @include m.telefono {
      max-width: 90%;
      text-align: justify;
    }
    @include m.tablet {
      max-width: 45%;
    }
  }
  &__link {
    background-color: v.$blanco;
    color: v.$negro;
    margin-top: 5rem;
    padding: 1rem 2rem;
    border-radius: 1rem;
  }
}
